<template>
  <div class="liveView">
    <video-player class="vjs-custom-skin"
                  ref="videoPlayer"
                  :options="playerOptions"
                  @ready="onPlayerReadied"
                  @timeupdate="onTimeupdate"
                  :playsinline="playsinline">
    </video-player>
    <button @click="funmusic()">2222</button>
    <iframe src="http://www.gai4.com/?url=https://vod.bunediy.com/20210519/oMpxq9X9/index.m3u8" height="'+MacPlayer.Height+'" width="100%" scrolling="no" id="Player" style="margin-top: 100px;"  allowfullscreen="true"></iframe>
  </div>
</template>
 
<script>
import {getlink} from "@/api/getapi.js"
import { videoPlayer } from 'vue-video-player'
import 'video.js/dist/video-js.css'
import 'vue-video-player/src/custom-theme.css'
import 'videojs-contrib-hls'
 
export default {
  name: 'live',
  components: {
    videoPlayer
  },
  data () {
    return {
      initialized: false,
      currentTech: '',
      streams: {
        rtmp: '',
        hls: ''
      },
      playerOptions: {
        overNative: true,
        autoplay: false,
        controls: true,
        techOrder: ['html5'],
        sourceOrder: true,
        flash: {
          hls: { withCredentials: false }
        },
        html5: { hls: { withCredentials: false } },
        sources: [
          {
            withCredentials: false,
            type: 'application/x-mpegURL',
            src: 'https://cache1.yaodian123.cn/Cache/qq/s0036n1hdl7.m3u8'
            // https://cache1.jhdyw.vip:8091/jianghu/qq/p0036mrfj8h.m3u8
            // https://cdn.letv-cdn.com/2018/12/05/JOCeEEUuoteFrjCg/playlist.m3u8
            // https://cache1.yaodian123.cn/Cache/qq/s0036n1hdl7.m3u8
            // https://sf1-ttcdn-tos.pstatp.com/obj/tos-cn-v-0004/bdfeb853e20244ff81db5dd3bfabb0f5
            // https://video.buycar5.cn/20200731/4MDPNiKl/index.m3u8
            // https://jx.444662.cn/m3u8/?url=


          }
        ]
      }
    }
  },
  computed: {
    player () {
      return this.$refs.videoPlayer.player
    },
    currentStream () {
      return this.currentTech === 'Flash' ? 'RTMP' : 'HLS'
    },
    playsinline () {
      let ua = navigator.userAgent.toLocaleLowerCase()
      // x5内核
      if (ua.match(/tencenttraveler/) != null || ua.match(/qqbrowse/) != null) {
        return false
      } else {
        // ios端
        return true
      }
    }
  },
  methods: {
    onPlayerReadied () {
      if (!this.initialized) {
        this.initialized = true
        this.currentTech = this.player.techName_
      }
    },
    // record current time
    onTimeupdate (e) {
      console.log('currentTime', e.cache_.currentTime)
    },
    funmusic(){

// this.vlink=document.querySelector("input").value;
    // console.log(this.$refs.getmusic.value);
    getlink(`/col/api.php/provide/vod/?ac=detail`).then((ok)=>{
  console.log(ok);
  // console.log(ok.data.url);
  // this.mlink=ok.data.url
  // MacPlayer.Html = '<iframe src="https://www.gai4.com/?url='+MacPlayer.PlayUrl+'" height="'+MacPlayer.Height+'" width="100%" scrolling="no" id="Player"  allowfullscreen="true"></iframe>';
MacPlayer.Show();

})

  }
  }
}
</script>
 
<style scoped>
.liveView {
  position: relative;
  
}
</style>


